body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  margin: 8px;
  cursor: pointer;
}

input[type="number"] {
  width: 40px;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.topLevel {
  width: 600px;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 451px;
}

.mainContent {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.buttonGroup {
  display: flex;
  justify-content: space-between;
  width: 100%;
  display: flex;
}

.largeButton {
  width: 160px;
  height: 36px;
}

.smallButtonÂ  {
  width: 80px;
  height: 36px;
}

.largeText {
  font-size: 80px;
  margin: 16px;
}

.smallText {
  font-size: 16px;
  font-style: italic;
  margin: 16px;
}

.error-items {
  display: flex;
}

.main-input {
  width: calc(100% - 16px);
  margin: 8px;
  padding: 4px;
  height: 48px;
  margin-bottom: 16px;
  font-size: 36px;
  text-align: center;
}

.input-group {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.input-item-group {
  display: flex;
  margin: 8px;
}

.secondary-input {
  margin-left: 8px;
}

@media only screen and (max-width: 768px) {
  .topLevel {
    width: 100%;
  }

  .largeText {
    font-size: 36px;
  }

  .main-input {
    font-size: 24px;
  }

  .buttonGroup {
    flex-direction: column-reverse;
    align-items: center;
  }
}
